"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import { API_BASE_URL } from "@/config/api";

interface Opportunity {
  id: number;
  title: string;
  description: string;
  fieldOfStudy: string;
  type: string;
  duration: string;
  prerequisites: string;
  pdfUrl: string;
}

export default function ProfessorDashboard() {
  const [opportunities, setOpportunities] = useState<Opportunity[]>([]);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    fetchOpportunities();
  }, []);

  const fetchOpportunities = async () => {
    try {
      // const res = await fetch(`${API_BASE_URL}/api/professor/opportunities`);
      // const data = await res.json();
      // setOpportunities(data);
      setOpportunities([
        {
          id: 1,
          title: "AI Research Internship",
          description: "Assist in ML research",
          fieldOfStudy: "AI/ML",
          type: "Internship",
          duration: "3 months",
          prerequisites: "Python, TensorFlow",
          pdfUrl: "/uploads/4d351a2f-5b4d-4668-85eb-3571b0daaf0a_test2.pdf",
        },
        {
          id: 2,
          title: "Updated Research on AI",
          description: "Now includes dataset section",
          fieldOfStudy: "ML",
          type: "Internship",
          duration: "5 months",
          prerequisites: "Python, TensorFlow",
          pdfUrl: "/uploads/637f5586-fdf3-4e2b-8e2d-60c2d02565dc_Ticket.pdf",
        },
        {
          id: 3,
          title: "Updated 3.0 Research on AI",
          description: "Now includes dataset section",
          fieldOfStudy: "ML",
          type: "Internship",
          duration: "5 months",
          prerequisites: "Python, TensorFlow",
          pdfUrl: "/uploads/139ae105-1b62-4e60-ac45-1aff61e52495_Ticket.pdf",
        },
      ]);
    } catch (error) {
      console.error("Error fetching opportunities:", error);
    }
    setLoading(false);
  };

  return (
    <div className="page">
      {/* Header */}
      <header className="bg-gradient-to-r from-blue-600 to-indigo-600 text-white p-5 shadow-md flex justify-between items-center">
        <h1 className="text-2xl font-bold">
          ProfConnect - Professor Dashboard
        </h1>
      </header>

      {/* Post New Opportunity Button */}
      <section className="p-6">
        <Link
          href="/professor/post-opportunity"
          className="bg-green-600 hover:bg-green-700 text-white font-semibold px-6 py-3 rounded-2xl shadow-md transition inline-block"
        >
          Post New Opportunity
        </Link>
      </section>

      {/* Filter Section */}
      <section className="bg-white shadow-md p-6 sticky top-0 z-10 flex items-center gap-4">
        <label className="font-medium text-gray-700">Filter:</label>
        <select
          id="professorFilter"
          className="border rounded-lg px-3 py-2 focus:ring focus:ring-blue-200"
        >
          <option value="">Select</option>
          <option>Institute Name</option>
          <option>Recently Added</option>
        </select>
      </section>

      {/* Opportunities Section */}
      <main
        className="p-6 grid gap-6 md:grid-cols-2 lg:grid-cols-3"
        id="professorOpportunities"
      >
        {loading && (
          <p className="text-center col-span-3">Loading opportunities...</p>
        )}

        {!loading && opportunities.length === 0 && (
          <p className="text-center col-span-3 text-gray-600">
            No opportunities posted yet.
          </p>
        )}

        {opportunities.map((opp) => (
          <div
            key={opp.id}
            className="bg-white p-5 rounded-xl shadow-lg border border-gray-100 hover:shadow-xl transition"
          >
            <h3 className="text-lg font-bold text-indigo-700">{opp.title}</h3>
            <p className="text-gray-600 text-sm mt-1">{opp.description}</p>

            <div className="mt-3 text-sm text-gray-700">
              <p>
                <strong>Field:</strong> {opp.fieldOfStudy}
              </p>
              <p>
                <strong>Type:</strong> {opp.type}
              </p>
              <p>
                <strong>Duration:</strong> {opp.duration}
              </p>
              <p>
                <strong>Prerequisites:</strong> {opp.prerequisites}
              </p>
            </div>

            <a
              href={`${API_BASE_URL}${opp.pdfUrl}`}
              target="_blank"
              className="mt-4 block bg-indigo-600 text-white text-center px-4 py-2 rounded-lg hover:bg-indigo-700 transition"
            >
              View PDF
            </a>
          </div>
        ))}
      </main>
    </div>
  );
}
